import React, { FC, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import ReactPlayer from "react-player";

import { useResultContext } from "../context/ResultContextProvider";
import Loading from "../components/Loading";
import Header from "../components/Header";
import Footer from "../components/Footer";
import MapComponent from "../components/MapComponent";
import ImageCard from "../components/ImageCard";
import VideoSearch from "../components/VideoSearch";

const Results: FC = () => {
  const { results, isLoading, getResults, searchTerm, selected, setSelected } =
    useResultContext();
  const location = useLocation();

  useEffect(() => {
    if (searchTerm) {
      // if (location.pathname === "/videos") {
      //   // setSelected(location.pathname);
      //   getResults(`/search/q=${searchTerm} videos`);
      // } else {
      //   console.log("hitting");
      //   setSelected(`/imagesearch`);
      //   getResults(`${location.pathname}/q=${searchTerm}&num=40`);
      // }
      if (location.pathname === "/news") {
        setSelected(location.pathname);
        getResults("/");
      } else {
        setSelected(location.pathname);
        getResults(location.pathname);
      }

      console.log(location.pathname);
    }
  }, [searchTerm, location.pathname]);

  if (isLoading) {
    return (
      <>
        <Header selected={selected} />
        <Loading />
      </>
    );
  }

  switch (selected) {
    case "/websearch":
      return (
        <>
          <Header selected={selected} />
          <div className="mx-auto w-full px-3 sm:pl-[5%] md:pl-[14%] lg:pl-52 mt-5">
            {results?.result?.map(({ href, title, body }: any, index: any) => (
              <div key={index} className="max-w-xl mb-8">
                <div className="group">
                  <a
                    href={href}
                    className="text-sm"
                    target="_blank"
                    rel="noreferrer"
                  >
                    {href.length > 50 ? href.substring(0, 50) : href}
                  </a>
                  <a href={href} target="_blank" rel="noreferrer">
                    <h2 className="truncate text-xl text-blue-800 font-medium group-hover:underline">
                      {title}
                    </h2>
                  </a>
                </div>
                <p className="line-clamp-2">{body}</p>
              </div>
            ))}
          </div>
          <Footer />
        </>
      );
    case "/imagesearch":
      return (
        <>
          <Header selected={selected} />
          <div className="flex flex-wrap justify-center items-center">
            {results?.result?.map(
              (
                {
                  image,
                  title,
                  url,
                  source,
                }: {
                  image: string;
                  title: string;
                  url: string;
                  source: string;
                },
                index: number
              ) => (
                <a
                  className="sm:p-3 p-5"
                  href={url}
                  key={index}
                  target="_blank"
                  rel="noreferrer"
                >
                  {/* <img
                    src={image}
                    alt={title}
                    loading="lazy"
                    height={height / 4}
                    width={width / 4}
                  /> */}
                  <ImageCard
                    title={title}
                    imgSrc={image}
                    source={source}
                    alt={title}
                  />
                  <p className="w-36 break-words text-sm mt-2 font-medium">
                    {title.length > 50 ? title.substring(0, 50) : title}
                  </p>
                </a>
              )
            )}
          </div>
        </>
      );
    case "/news":
      return (
        <>
          <Header selected={selected} />
          <div className="flex flex-wrap justify-between space-y-6 sm:px-56 items-center mb-10">
            {results?.news?.map(({ url, source, title }: any) => (
              <div className="md:w-2/5 w-full">
                <a
                  href={url}
                  target="_blank"
                  rel="noreferrer"
                  className="hover:underline"
                >
                  <p className="text-lg dark:text-blue-300 text-blue-800">
                    {title}
                  </p>
                </a>
                <div className="flex gap-4">
                  <a href={url} target="_blank" rel="noreferrer">
                    {source}
                  </a>
                </div>
              </div>
            ))}
          </div>
          <Footer />
        </>
      );
    case "/videosearch":
      return (
        <>
          <Header selected={selected} />
          <VideoSearch selected="Videos" video={results} />
        </>
      );
    case "/map":
      return (
        <>
          <Header selected={selected} />
          <MapComponent results={results} />
        </>
      );
    default:
      return <h1>"Error!"</h1>;
  }
};

export default Results;
