// ImageCard.tsx
import React from "react";

interface ImageCardProps {
  title: string;
  imgSrc: string;
  source: string;
  alt: string;
}

const ImageCard: React.FC<ImageCardProps> = ({
  title,
  imgSrc,
  source,
  alt,
}) => {
  return (
    <div style={styles.card}>
      <img src={imgSrc} alt={alt} style={styles.image} />
      <div style={styles.content}>
        <h3 style={styles.title}>{title}</h3>
        <p style={styles.source}>{source}</p>
      </div>
    </div>
  );
};

const styles = {
  card: {
    width: "200px",
    margin: "10px",
    border: "1px solid #ddd",
    borderRadius: "8px",
    overflow: "hidden",
    boxShadow: "0 2px 5px rgba(0, 0, 0, 0.1)",
  },
  image: {
    width: "100%",
    height: "auto",
  },
  content: {
    padding: "10px",
  },
  title: {
    fontSize: "16px",
    margin: "0 0 10px 0",
  },
  source: {
    fontSize: "12px",
    color: "#888",
  },
};

export default ImageCard;
