import React, { useState } from "react";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

// Data type for your results
interface Result {
  title: string;
  address: string;
  latitude: number;
  longitude: number;
  source: string;
  hours: Record<string, string>;
  category: string;
  phone: string;
  google_maps: string;
}

// Define the props for the component
interface MapProps {
  results: { result: Result[] };
}

const MapComponent: React.FC<MapProps> = ({ results }) => {
  const [selectedResult, setSelectedResult] = useState<Result | null>(null);

  // Create a custom icon for the markers
  const customIcon = new L.Icon({
    iconUrl:
      "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png",
    iconSize: [25, 41],
    iconAnchor: [12, 41],
  });

  return (
    <div className="flex">
      <div className="w-1/2 h-screen">
        <MapContainer
          center={[38.8976817, -77.036588]}
          zoom={13}
          style={{ height: "100%", width: "100%" }}
        >
          <TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          />
          {results.result.map((result, index) => (
            <Marker
              key={index}
              position={[result.latitude, result.longitude]}
              icon={customIcon}
              eventHandlers={{
                click: () => {
                  setSelectedResult(result);
                },
              }}
            >
              <Popup>{result.title}</Popup>
            </Marker>
          ))}
        </MapContainer>
      </div>
      <div className="w-1/2 h-screen p-4 overflow-auto">
        {selectedResult ? (
          <div>
            <h2>{selectedResult.title}</h2>
            <p>{selectedResult.address}</p>
            <p>{selectedResult.phone}</p>
            <p>{selectedResult.category}</p>
            <a
              href={selectedResult.google_maps}
              target="_blank"
              rel="noopener noreferrer"
            >
              View on Google Maps
            </a>
            <p>Hours:</p>
            <ul>
              {Object.entries(selectedResult.hours).map(([day, hours], idx) => (
                <li key={idx}>
                  {day}: {hours}
                </li>
              ))}
            </ul>
            <a
              href={selectedResult.source}
              target="_blank"
              rel="noopener noreferrer"
            >
              Source
            </a>
          </div>
        ) : (
          <p>Click on a marker to see details</p>
        )}
      </div>
    </div>
  );
};

export default MapComponent;
