import React from "react";
import ReactPlayer from "react-player";

interface Video {
  content: string;
  description: string;
  duration: string;
  embed_html: string;
  embed_url: string;
  images: {
    large: string;
    medium: string;
    motion: string;
    small: string;
  };
  provider: string;
  published: string;
  publisher: string;
  statistics: {
    viewCount: number;
  };
  title: string;
  uploader: string;
}

interface VideoSearchProps {
  selected: string;
  video: { result: Video[] };
}

const VideoSearch: React.FC<VideoSearchProps> = ({ selected, video }) => {
  return (
    <>
      <div className="flex flex-wrap mx-auto w-full px-3 sm:pl-[5%] md:pl-[14%] lg:pl-52 mt-5">
        {video?.result?.map((video: Video, index: number) => (
          <div className="p-2 w-full flex" key={index}>
            <ReactPlayer
              url={video.embed_url}
              controls
              width="255px"
              height="100px"
            />
            <div className="ml-4 flex flex-col justify-between">
              <p className="text-sm font-semibold">{video.title}</p>
              <p className="text-xs text-gray-600">
                {video.description.length > 100
                  ? video.description.substring(0, 100) + "..."
                  : video.description}
              </p>
              <p className="text-xs">Duration: {video.duration}</p>
              <p className="text-xs">Uploaded by: {video.uploader}</p>
            </div>
          </div>
        ))}
      </div>
    </>
  );
};

export default VideoSearch;
