import React, {
  createContext,
  Dispatch,
  FC,
  ReactNode,
  SetStateAction,
  useContext,
  useState,
} from "react";

interface ContextInterface {
  getResults: (data: string) => void;
  results: object | any;
  searchTerm: string;
  setSearchTerm: Dispatch<SetStateAction<string>>;
  selected: string;
  setSelected: Dispatch<SetStateAction<string>>;
  isLoading: boolean;
}

export const ResultContext = createContext<ContextInterface>({
  getResults: () => {},
  results: [],
  searchTerm: "",
  setSearchTerm: () => {},
  selected: "",
  setSelected: () => {},
  isLoading: false,
});

interface Props {
  children: ReactNode;
}

export const ResultContextProvider: FC<Props> = ({ children }) => {
  const [results, setResults] = useState<object>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [selected, setSelected] = useState<string>("/websearch");

  const getResults = async (type: string) => {
    console.log("selected", type);

    setIsLoading(true);

    let Body: string | undefined;

    switch (type) {
      case "/imagesearch":
        Body = JSON.stringify({
          text: searchTerm,
          safesearch: "off",
          region: "wt-wt",
          color: "",
          size: "",
          type_image: "",
          layout: "",
          max_results: 100,
        });
        break;

      case "/websearch":
        Body = JSON.stringify({
          text: searchTerm,
          safesearch: "off",
          timelimit: "",
          region: "wt-wt",
          max_results: 20,
        });
        break;

      case "/videosearch":
        Body = JSON.stringify({
          text: searchTerm,
          safesearch: "off",
          timelimit: "",
          duration: "",
          resolution: "",
          region: "wt-wt",
          max_results: 50,
        });
        break;
      case "/":
        Body = JSON.stringify({
          text: searchTerm,
          region: "wt-wt",
          max_results: 25,
        });
        break;
      case "/map":
        Body = JSON.stringify({
          text: searchTerm,
          place: searchTerm,
          street: "",
          city: "",
          country: "",
          state: "",
          postalcode: "",
          latitude: "",
          longitude: "",
          radius: "",
        });
        break;

      default:
        throw new Error("Unsupported search type");
    }
    if (searchTerm != "") {
      const response = await fetch(
        `https://google-api31.p.rapidapi.com${type}`,
        {
          method: "POST",
          headers: {
            "x-rapidapi-key": process.env.REACT_APP_API_KEY
              ? process.env.REACT_APP_API_KEY
              : " ",
            "x-rapidapi-host": "google-api31.p.rapidapi.com",
            "Content-Type": "application/json",
          },
          body: Body,
        }
      );

      const data = await response.json();

      console.log("data", data);
      setResults(data);
      setIsLoading(false);
    }
  };

  const sampleAppContext: ContextInterface | null = {
    getResults,
    results,
    searchTerm,
    setSearchTerm,
    isLoading,
    selected,
    setSelected,
  };

  return (
    <ResultContext.Provider value={sampleAppContext}>
      {children}
    </ResultContext.Provider>
  );
};

export const useResultContext = () => useContext(ResultContext);
